import { useEffect, useLayoutEffect, useState } from "react"
import dayjs, { Dayjs } from 'dayjs';
import arraySupportPlugin from 'dayjs/plugin/arraySupport';


dayjs.extend(arraySupportPlugin);
const today = dayjs();
// eslint-disable-next-line import/no-anonymous-default-export
export default (value?: Dayjs):any => {
    const [year, setYear] = useState() as any;
    const [month, setMonth] = useState() as any;
    useLayoutEffect(() => {
       const current =  value || today;
       setYear(current.year());
       setMonth(current.month())
    }, [])

    return [year, month, setYear, setMonth]
}
