
import { useEffect, useState } from "react";
import Month from "./Month";
import useRange from "./useRange";
import dayjs, { Dayjs } from "dayjs";
import styles from './index.less'
import Popover from "component/Popover";
import Year from "./Year";
import Date from './Date'


const RangeView = (props: any) => {
    const { picker, onChange, value = [], disableDate = () => { }, placeHolder } = props;
    const [state, action] = useRange();
    const [template, setTemplate] = useState() as any;
    const [visible, setVisible] = useState(false)
    useEffect(() => {
        action(
            {
                type: 'reset',
                mode: picker,
                value: value.length ? {
                    start: dayjs(value[0]),
                    end: dayjs(value[1])
                } : null
            }
        )
    }, [value]);
    useEffect(() => {
        if (value.length) {
            setTemplate({
                start: dayjs(value[0]),
                end: dayjs(value[1])
            })
        }

    }, [])

    const handleChangeYear = (type: string) => {
        action({
            type: picker,
            operator: type,
            year: state.startYear,
        })
    }
    const handleChangeMonth = (type: string) => {
        action({
            type: 'monthDay',
            operator: type,
            year: state.startYear,
            month: state.startMonth
        })
    }

    const handleChangeValue = (t: Dayjs, tip: boolean) => {
        const { start, end } = template || {};
        if (start && end || !start && !end) {
            setTemplate({ start: t, end: '' })
        } else {
            if (start && !end) {
                if (start.isBefore(t)) {
                    setTemplate({ start, end: t })
                    onChange([start, t])
                } else {
                    setTemplate({ start: t, end: start })
                    onChange([t, start])
                }
                setVisible(false);


            }
        }
    }
   
    const renderVal = () => {
        const { start, end } = template || {};
        let str = '';
        let format = 'YYYY-MM-DD'
        if(picker === 'month') {
            format= 'YYYY-MM'
        }
        if(picker === 'year') {
            format='YYYY'
        }
        if (start) {
            str = start.format(format)
        }
        if (end) {
            str += '~' + end.format(format)
        }
        return str;
    }
    const renderContent = (): JSX.Element => {
        switch (picker) {

            case 'day': {
                return <div style={{ display: 'flex' }}>
                    <Date
                        year={state.startYear}
                        handleChangeYear={handleChangeYear}
                        end
                        disableDate={disableDate}
                        value={template}
                        onChange={handleChangeValue}
                        range
                        month={state.startMonth}
                        handleChangeMonth={handleChangeMonth}
                    />
                    <Date
                        year={state.endYear}
                        handleChangeYear={handleChangeYear}
                        start
                        disableDate={disableDate}
                        value={template}
                        onChange={handleChangeValue}
                        range
                        month={state.endMonth}
                        handleChangeMonth={handleChangeMonth}
                    />
                </div>
            }

            case 'month': {
                return <div style={{ display: 'flex' }}>
                    <Month
                        year={state.startYear}
                        handleChangeYear={handleChangeYear}
                        end
                        disableDate={disableDate}
                        value={template}
                        onChange={handleChangeValue}
                        range
                    />
                    <Month
                        year={state.endYear}
                        handleChangeYear={handleChangeYear}
                        start
                        disableDate={disableDate}
                        value={template}
                        onChange={handleChangeValue}
                        range
                    />
                </div>
            }
            case 'year': {
                return <div style={{ display: 'flex' }}>
                    <Year
                        year={state.startYear}
                        handleChangeYear={handleChangeYear}
                        end
                        disableDate={disableDate}
                        value={template}
                        onChange={handleChangeValue}
                        range
                    />
                    <Year
                        year={state.endYear}
                        handleChangeYear={handleChangeYear}
                        start
                        disableDate={disableDate}
                        value={template}
                        onChange={handleChangeValue}
                        range
                    />
                </div>
            }

        }
        return <></>
    }


    const handleVisible = () => {
        const { start, end } = template || {};
        if (!(start && end)) {
            setTemplate(null);
            onChange([])
        } else {
            onChange([start, end])
        }
        setVisible(false);
    }

    

    return (

        <div className={styles.con}>

            <Popover
                placeholder="BL"
                content={renderContent()}
                visible={visible}
                trigged="popover"
                isArrow={false}
                onVisibleChange={handleVisible}
                overlayClass="datePick"
            >

                <span onClick={() => setVisible(true)} >
                    <input className={styles.input} type="text" value={renderVal()} readOnly spellCheck={false} placeholder={placeHolder} />
                </span>
            </Popover>


        </div>
    )
}

export default RangeView;