


import dayjs, { Dayjs } from 'dayjs';
import arraySupportPlugin from 'dayjs/plugin/arraySupport';
import { useReducer } from 'react';

const today = dayjs();

const initialState = {
    startMonth: today.month(),
    startYear: today.year(),
    endMonth: today.add(1, 'month').month(),
    endYear: today.add(1, 'month').year(),
}
// 传入的值是什么格式 {start, end}

const action = (state: any, actions: any) => {
    if (actions.type === 'reset') {
        const { mode, value } = actions;
        const { start } = value || { start: today };
        const newState = {
            startYear: start.year(),
            startMonth: start.month(),
        } as any;
        if (mode === 'day') {
            newState.endYear = start.year();
            newState.endMonth = start.add(1, 'month').month();
        }
        if (mode === 'month') {
            newState.endYear = start.year() + 1;
            newState.endMonth = start.month();
        }
        if (mode === 'year') {
            newState.endYear = start.year() + 10;
            newState.endMonth = start.month();
        }

        return { ...state, ...newState }
    }

    if (actions.type === 'month') {
        const { year, operator } = actions;
        const newState = {
            startYear: operator === '-' ? year - 1 : year + 1,
            endYear: operator === '-' ? year : year + 2
        }
        return { ...state, ...newState }
    }

    if (actions.type === 'year') {
        const { year, operator } = actions;
        const newState = {
            startYear: operator === '-' ? year - 10 : year + 10,
            endYear: operator === '-' ? year : year + 20
        }
        return { ...state, ...newState }
    }
    if (actions.type === 'day') {
        const { year, operator } = actions;
        const newState = {
            startYear: operator === '-' ? year - 1 : year + 1,
            endYear: operator === '-' ? year : year + 2
        }
        return { ...state, ...newState }
    }
    if(actions.type === 'monthDay') {
        const { year, operator, month } = actions;
        const day = dayjs(`${year}-${month}`);
        const start= operator === '-' ?  day.subtract(1, 'month') : day.add(2,'month');
        const end = operator === '-' ? day : day.add(3,'month');
        const newState = {
            startYear: start.year(),
            startMonth: start.month(),
            endYear: end.year(),
            endMonth: end.month(),
        }
       
        return { ...state, ...newState}
    }

    return state;
}

export default () => {
    const [state, dispatch] = useReducer(action, initialState);
    return [state, dispatch]
}