
import Date from './Date'
import Popover from 'component/Popover';
import { useEffect, useRef, useState } from 'react';
import styles from './index.less'
import dayjs from 'dayjs';
import useYearMonth from './useYearMonth';
interface Props {
    onChange?: (time: any) => void;
    value?: string;
    picker?: string;
    disableDate?: (time: any) => boolean;
}
const DatePick = (props: Props) => {
    const { value, onChange, disableDate } = props;
    const [visible, setVisible] = useState(false);
    const [template, setTemplate] = useState('');
    const [year, month, setYear, setMonth] = useYearMonth(value ? dayjs(value) : undefined);

    useEffect(() => {
        if (value) {
            setTemplate(value)
        }
    }, [value])
    const handleChangeTime = (t: any) => {
        setVisible(false);
        setTemplate(t.format('YYYY-MM-DD'))
        if (onChange) {
            onChange(t)
        }
        setYear(t.format('YYYY'));
        setMonth(t.format('MM') - 1)
    }

    const handleChangeYear = (type: string) => {
        setYear((old: number) => {
            if (type === '+') {
                return old + 1;
            }
            return old - 1;
        })
    }

    const handleChangeMonth = (type: string) => {
        setMonth((old: number) => {
            if (type === '+') {
                return old + 1;
            }
            return old - 1;
        })
    }

    return (
        <div className={styles.con}>
            <Popover
                placeholder="BL"
                content={<Date
                    value={template ? dayjs(template) : undefined}
                    onChange={handleChangeTime}
                    disableDate={disableDate}
                    year={year}
                    month={month}
                    handleChangeYear={handleChangeYear}
                    handleChangeMonth={handleChangeMonth}

                />}
                visible={visible}
                trigged="popover"
                isArrow={false}
                onVisibleChange={() => setVisible(false)}
                overlayClass="datePick"
            >

                <span onClick={() => setVisible(true)} >
                    <input className={styles.input} type="text" value={template} readOnly spellCheck={false} placeholder="请选择日期" />
                </span>
            </Popover>


        </div>
    )
}

export default DatePick;