export default "\n\nimport DatePick from 'component/DatePicker'\nimport { useState } from 'react'\nimport styles from './index.less'\nimport dayjs, { Dayjs } from 'dayjs'\nimport Year from 'component/DatePicker/Year'\nimport YearView from 'component/DatePicker/YearView'\nconst disableCb = (t: Dayjs): boolean => t.isBefore(dayjs())\nconst BaseView = () => {\n    const [time, setTime] = useState('');\n    const [month, setMonth] = useState('');\n    const [year, setYear] = useState('');\n    const handleTime = (t: Dayjs) => {\n        setTime(t.format('YYYY-MM-DD'))\n    }\n    const handleMonth = (t: Dayjs) => {\n        setMonth(t.format('YYYY-MM'))\n    }\n    const handleYear = (t: Dayjs)=>{\n        setYear(t.format('YYYY'))\n    }\n    return (\n        <div>\n            <div className={styles.row}>\n                <div className={styles.text}>日期选择:\n                </div>\n                <DatePick value={time} onChange={handleTime} picker=\"day\" />\n            </div>\n            <div className={styles.row}>\n                <div className={styles.text}>月份选择:\n                </div>\n                <DatePick value={month} onChange={handleMonth} picker=\"month\" disableDate={disableCb} />\n            </div>\n\n            <div className={styles.row}>\n                <div className={styles.text}>年选择:\n                </div>\n                <DatePick value={year} onChange={handleYear} picker=\"year\" disableDate={disableCb} />\n            </div>\n        </div>\n    )\n}\n\nexport default BaseView";