
import Date from './Date'
import Popover from 'component/Popover';
import { useEffect, useRef, useState } from 'react';
import styles from './index.less'
import dayjs from 'dayjs';
import Month from './Month';
import useYearMonth from './useYearMonth';
interface Props {
    onChange?: (time: any) => void;
    value?: string;
    picker?: string;
    disableDate?:(time:any)=> boolean;
}
const MonthView = (props: Props) => {
    const { value, onChange, disableDate} = props;
    const [visible, setVisible] = useState(false);
    const [template, setTemplate] = useState('');
    const [year, month, setYear, setMonth] = useYearMonth(value ? dayjs(value):undefined);
    useEffect(() => {
        if (value) {
            setTemplate(value)
        }
    }, [value])
    
    const handleChangeTime = (t: any) => {
        setVisible(false);
        setTemplate(t.format('YYYY-MM'))
        if (onChange) {
            onChange(t)
        }
    }

    const handleChangeYear = (type:string) => {
         setYear((old: number) => {            
            if (type === '+') {
                return old + 1;
            }
            return old - 1;
        })
    }
    
    return (
        <div className={styles.con}>
            
            <Popover
                placeholder="BL"
                content={<Month
                    value={template ? dayjs(template) : undefined}
                    onChange={handleChangeTime}
                    disableDate={disableDate}
                    year={year}
                    handleChangeYear={handleChangeYear}
                />}
                visible={visible}
                trigged="popover"
                isArrow={false}
                onVisibleChange={() => setVisible(false)}
                overlayClass="datePick"
            >

                <span onClick={() => setVisible(true)} >
                    <input className={styles.input} type="text" value={template} readOnly spellCheck={false} placeholder="请选择月份" />
                </span>
            </Popover>


        </div>
    )
}

export default MonthView;