// 难点  如何知道日期跟星期几对应
// 算法是什么

import styles from './index.less'
import dayjs, { Dayjs } from 'dayjs';
import { useEffect, useMemo, useState } from 'react';
import arraySupport from 'dayjs/plugin/arraySupport';
import classnames from 'classnames'
import useYearMonth from './useYearMonth';
import { debounce } from 'lodash';
import LeftDoubleIcon from 'component/Icon/LeftDoubleIcon';
import LeftIcon from 'component/Icon/LeftIcon';
import RightDoubleIcon from 'component/Icon/RightDoubleIcon';
import RightIcon from 'component/Icon/RightIcon';
dayjs.extend(arraySupport); // 不加这个 7月就是7月

const today = dayjs();

const defaultDisableDate = (): boolean => false;

type BridgeCb = (date?: Dayjs) => void;
// 为什么写这个，两个item 就更新 全级变量 两边更新
class PickerTabuleSyncBridge {
    private map = new Map<symbol, BridgeCb[]>();

    private valueMap = new Map<symbol, Dayjs | undefined>();

    public add(key: symbol, cb: (date?: Dayjs) => void): void {
        let arr = this.map.get(key);
        if (arr === undefined) {
            arr = [cb];
        } else {
            arr.push(cb);
        }
        this.map.set(key, arr);
    }

    public remove(key: symbol, cb: (date?: Dayjs) => void): void {
        const arr = this.map.get(key);
        if (arr) {
            this.map.set(key, arr.filter((item) => item !== cb));
        }
    }

    public sync = debounce((key: symbol, value?: Dayjs) => {
        const arr = this.map.get(key);
        const lastValue = this.valueMap.get(key);
        if (lastValue !== value && arr) {
            arr.forEach((cb) => {
                cb(value);
            });
            this.valueMap.set(key, value);
        }
    }, 50);
}

// 两次应用为什么不是创建老公变量-
const bridge = new PickerTabuleSyncBridge();



const Date = (props: any) => {
    const {
        value, disableDate = defaultDisableDate, onChange, style,
        year, month, handleChangeYear, handleChangeMonth, pickerKey = "day",
        start, end, range,
    } = props;
    const [hoverValue, setHoverValue] = useState() as any;

    useEffect(() => {
        bridge.add(pickerKey, setHoverValue);
        return () => {
            bridge.remove(pickerKey, setHoverValue);
        };
    }, [pickerKey, setHoverValue]);
    // const [year, month, setYear, setMonth] =  useYearMonth(value);
    const data: any = useMemo(() => {
        const thisMonth = dayjs([year, month]); //计算一号在星期几// 8月1好 在 星期 日0
        const weekDay = thisMonth.day(); // month + 1 的月份 1号在那天
        const list = []; // 造六条数据 ，每条数据 有七个值
        let addDay = 1; // 计算一号之后的日期
        // 每一条数据长什么样
        const makeData = (date: any, key: any) => ({
            value: date,
            text: date.format('DD'),
            key: key,
            isCurrentMonth: date.isSame(thisMonth, 'month') === false,
            disable: disableDate(date),
            tip: date.isSame(thisMonth, 'month') === false,
        })
        // 6条数据
        for (let i = 0; i < 6; i += 1) {
            const thisWeek = []; // 每条数据有七个值
            for (let weekend = 0; weekend < 7; weekend += 1) {
                const key = `${thisMonth.month()}_${i}_${weekend}`;
                // 先一个星期的，主要是补星期一前面的日期
                if (i === 0) {
                    if (weekend <= weekDay) {
                        const date = thisMonth.subtract(weekDay - weekend, 'day');
                        thisWeek.push(makeData(date, key));
                        continue;
                    }
                }

                const date = thisMonth.add(addDay, 'day');
                thisWeek.push(makeData(date, key));
                addDay++; // 日期递增
            }

            list.push({
                key: `${thisMonth.month()}_${i}`,
                cels: thisWeek,
            })
        }

        return {
            list,
            year: `${thisMonth.format('YYYY')}年`,
            month: `${thisMonth.format('MM')}月`,
        }
    }, [disableDate, month, year])


    const handleYear = (type: string) => {
        // setYear((old: number) => {
        //     if (type === '+') {
        //         return old + 1;
        //     }

        //     return old - 1
        // })
        handleChangeYear(type)
    }

    const handleMonth = (type: string) => {
        // setMonth((old: number) => {
        //     if (type === '+') {
        //         return old + 1;
        //     }

        //     return old - 1
        // })
        handleChangeMonth(type)

    }

    const handleSelect = (item: any) => {
        // 选择不是当月的日期
        onChange(item.value)
        // setYear(item.value.format('YYYY'));
        // setMonth(item.value.format('MM') - 1)
    }

    return (
        <div className={styles.container} style={style}>
            <div className={styles.header}>
                {
                    start ? <span></span> : (
                        <><span className={styles.cursor} onClick={() => handleYear('-')}>
                            <LeftDoubleIcon />
                        </span>
                            <span className={styles.cursor} onClick={() => handleMonth('-')}>
                                <LeftIcon />
                            </span></>
                    )
                }

                <span className={styles.text}>{data.year}-{data.month}</span>
                {
                    end ? <span></span> : (
                        <> <span className={styles.cursor} onClick={() => handleMonth('+')}>
                            <RightIcon />
                        </span>
                            <span className={styles.cursor} onClick={() => handleYear('+')}>
                                <RightDoubleIcon />
                            </span></>
                    )
                }

            </div>
            <div className={styles.row}>
                <span className={styles.item}>日</span>
                <span className={styles.item}>一</span>
                <span className={styles.item}>二</span>
                <span className={styles.item}>三</span>
                <span className={styles.item}>四</span>
                <span className={styles.item}>五</span>
                <span className={styles.item}>六</span>
            </div>
            {
                data.list.map((item: any) => (
                    <div className={styles.row} key={item.key}>
                        {
                            item.cels.map((sub: any) => {
                                let isStart = false;
                                let isStartWidthLeftBack = false; // 为了一边是浅绿色
                                let isStartWidthRightBack = false;
                                let isEnd = false;
                                let isEndWidthLeftBack = false; // 为了一边是浅绿色
                                let isEndWidthRightBack = false;
                                let isBetween = false; // 之间显示浅绿色
                                // 判断每个时间是否在其中
                                if (value && sub.tip === false) {
                                    const { start, end } = value;
                                   
                                    if (start && sub.value.isSame(start, 'day')) {
                                        isStart = true;
                                    }
                                    // 单选没有end
                                    if (end) {
                                        if (sub.value.isSame(end, 'day')) {
                                            isEnd = true; // tongyige
                                            if (!isStart) { // 不同一个
                                                isEndWidthLeftBack = true;
                                            }
                                        } else if (isStart && end.isAfter(start, 'day')) {
                                            isStartWidthRightBack = true;
                                        } else if (sub.value.isAfter(start, 'day') && sub.value.isBefore(end, 'day')) {
                                            isBetween = true;
                                        }
                                    } else if (hoverValue) {

                                        if (isStart) {
                                            isStartWidthLeftBack = hoverValue.isBefore(sub.value, 'day');
                                            isStartWidthRightBack = hoverValue.isAfter(sub.value, 'day')
                                        } else if (sub.value.isSame(hoverValue, 'day')) {
                                            isEndWidthLeftBack = hoverValue.isAfter(start, 'day');
                                            isEndWidthRightBack = hoverValue.isBefore(start, 'day');
                                        } else if ((sub.value.isAfter(hoverValue) && sub.value.isBefore(start, 'day'))
                                            || (sub.value.isBefore(hoverValue) && sub.value.isAfter(start, 'day'))) {
                                            isBetween = true;
                                        }
                                    }
                                }
                                return (
                                    <span className={classnames(styles.dateItem, {
                                        [styles.disable]: sub.disable,
                                        [styles.interactive]: !sub.disable,
                                        [styles.border]: today.isSame(sub.value, 'day') && sub.isCurrentMonth === false,
                                        [styles.noCurrentMonth]: sub.isCurrentMonth,
                                        [styles.selected]: (value && sub.value.isSame(value, 'day') && sub.isCurrentMonth === false) || isStart || isEnd,
                                        // [styles.selected]: value && sub.value.isSame(value, 'year') || isStart || isEnd,
                                        [styles.between]: isBetween,
                                        [styles.leftBack]: isStartWidthLeftBack || isEndWidthLeftBack,
                                        [styles.rightBack]: isStartWidthRightBack || isEndWidthRightBack,
                                    })}
                                        key={sub.key}
                                        onClick={() => {
                                            if (!sub.disable) {
                                                handleSelect(sub)
                                            }

                                        }}

                                        onMouseEnter={() => {
                                            // setHoverValue(sub.value);
                                            if (!range) {
                                                return
                                            }
                                            if (sub.disable || (value && value.end) || value === undefined) {
                                                bridge.sync(pickerKey);
                                            } else {
                                                bridge.sync(pickerKey, sub.value);
                                            }
                                        }}
                                        onMouseLeave={() => {
                                            // setHoverValue(null)
                                            if (!range) {
                                                return
                                            }
                                            bridge.sync(pickerKey);
                                        }}
                                    >
                                        <span className={styles.minItem}>{sub.text}</span>
                                    </span>
                                )
                            })
                        }
                    </div>
                ))
            }
        </div>
    )
}

export default Date;
