import { useEffect, useMemo, useState } from 'react';
import styles from './index.less'
import dayjs, { Dayjs } from 'dayjs';
import useYearMonth from './useYearMonth';
import arraySupport from 'dayjs/plugin/arraySupport';
import classnames from 'classnames';
import { debounce } from 'lodash';
import LeftDoubleIcon from 'component/Icon/LeftDoubleIcon';
import RightDoubleIcon from 'component/Icon/RightDoubleIcon';
dayjs.extend(arraySupport);
const defaultDisableDate = (date: Dayjs) => false;
const today = dayjs();
type BridgeCb=(date?:Dayjs)=>void;
// 为什么写这个，两个item 就更新 全级变量 两边更新
class PickerTabuleSyncBridge {
    private map=new Map<symbol, BridgeCb[]>();

    private valueMap =new Map<symbol, Dayjs|undefined>();

    public add(key:symbol, cb:(date?:Dayjs)=>void):void {
        let arr = this.map.get(key);
        if (arr === undefined) {
            arr = [cb];
        } else {
            arr.push(cb);
        }
        this.map.set(key, arr);
    }

    public remove(key:symbol, cb:(date?:Dayjs)=>void):void{
        const arr = this.map.get(key);
        if (arr) {
            this.map.set(key, arr.filter((item) => item !== cb));
        }
    }

    public sync=debounce((key:symbol, value?:Dayjs) => {
        const arr = this.map.get(key);
        const lastValue = this.valueMap.get(key);
        if (lastValue !== value && arr) {
            arr.forEach((cb) => {
                cb(value);
            });
            this.valueMap.set(key, value);
        }
    }, 50);
}

// 两次应用为什么不是创建老公变量-
const bridge = new PickerTabuleSyncBridge();

const Month = (props: any): JSX.Element => {
    const {
        value, disableDate = defaultDisableDate, onChange = () => { }, style,
        handleChangeYear = () => { }, year, end, start, pickerKey="month", range = false
    } = props;

    const [hoverValue, setHoverValue] = useState() as any;
   

    useEffect(() => {
        bridge.add(pickerKey, setHoverValue);
        return () => {
            bridge.remove(pickerKey, setHoverValue);
        };
    }, [pickerKey, setHoverValue]);

    
    // const [year, month, setYear, setMonth] = useYearMonth(value);
    // const [value, setValue] = useState();

    const data = useMemo(() => {
        // 这个月有一天是加=假，这个月九显示
        const calMonthDisabled = (month: Dayjs): boolean => {
            const thisMonthDay = month.daysInMonth();
            for (let i = 1; i <= thisMonthDay; i += 1) {
                const thisDay = month.set('date', i);
                if (disableDate(thisDay) === false) {
                    return false;
                }
            }
            return true;
        };
        const makeDate = (date: Dayjs, key: string) => ({
            value: date,
            title: `${date.month() + 1} 月`,
            disable: calMonthDisabled(date),
            key,
            tip: false,
        })
        let result = [];

        for (let i = 0; i < 4; i++) {
            let months = [];
            for (let j = 0; j < 3; j++) {
                let month = i * 3 + j + 1;
                const thisMonth = dayjs([year, month - 1]);
                const key = `${i}_${j}_${thisMonth.year()}`
                months.push(makeDate(thisMonth, key))

            }
            result.push({
                key: `${year}_${i}`,
                cells: months
            })
        }
        return {
            lists: result,
            year: `${year}年`
        };
    }, [year])

    const handleYear = (type: string) => {
        // handleChangeYear((old: number) => {
        //     if (type === '+') {
        //         return old + 1;
        //     }
        //     return old - 1;
        // })
        handleChangeYear(type)
    }

    const handleSelect = (t: any) => {
        onChange(t)
    }

    return (
        <div className={styles.container}>
            <div className={styles.monthHeader}>
                {
                    start ? <span></span> : <span className={styles.cursor} onClick={() => handleYear('-')}>
                        <LeftDoubleIcon />
                    </span>
                }

                <span>{data.year}</span>
                {
                    end ? <span></span> : <span className={styles.cursor} onClick={() => handleYear('+')}>
                        <RightDoubleIcon />
                    </span>
                }

            </div>
            {
                data.lists.map((item) => (
                    <div className={styles.row} key={item.key}>
                        {
                            item.cells.map((sub) => {
                                let isStart = false;
                                let isStartWidthLeftBack = false; // 为了一边是浅绿色
                                let isStartWidthRightBack = false;
                                let isEnd = false;
                                let isEndWidthLeftBack = false; // 为了一边是浅绿色
                                let isEndWidthRightBack = false;
                                let isBetween = false; // 之间显示浅绿色
                                // 判断每个时间是否在其中
                                if (value && sub.tip === false) {
                                    const { start, end } = value;
                                   
                                    
                                    if (start && sub.value.isSame(start, 'month')) {
                                        isStart = true;
                                    }
                                    // 单选没有end
                                    if (end) {
                                        if (sub.value.isSame(end, 'month')) {
                                            isEnd = true; // tongyige
                                            if (!isStart) { // 不同一个
                                                isEndWidthLeftBack = true;
                                            }
                                        } else if (isStart && end.isAfter(start, 'mode')) {
                                            isStartWidthRightBack = true;
                                        } else if(sub.value.isAfter(start,'month') && sub.value.isBefore(end,'month')) {
                                            isBetween = true;    
                                        }
                                    } else if (hoverValue) {
                                  
                                    if(isStart) {
                                        isStartWidthLeftBack = hoverValue.isBefore(sub.value, 'month');
                                        isStartWidthRightBack = hoverValue.isAfter(sub.value, 'month')
                                    } else if(sub.value.isSame(hoverValue, 'month')) {
                                        isEndWidthLeftBack = hoverValue.isAfter(start, 'month');
                                        isEndWidthRightBack = hoverValue.isBefore(start, 'month');
                                    } else if((sub.value.isAfter(hoverValue) && sub.value.isBefore(start, 'month'))
                                    || (sub.value.isBefore(hoverValue) && sub.value.isAfter(start, 'month'))) {
                                        isBetween = true;
                                    }      
                                }
                            }
                                
                                
                                return (
                                    <span className={classnames(styles.dateItem, {
                                        [styles.disable]: sub.disable,
                                        [styles.interactive]: !sub.disable,
                                        [styles.border]: today.isSame(sub.value, 'month'),
                                        // [styles.noCurrentMonth]: sub.isCurrentMonth,
                                        [styles.selected]: value && sub.value.isSame(value, 'month') || isStart || isEnd,
                                        [styles.between]:  isBetween,
                                        [styles.leftBack]: isStartWidthLeftBack || isEndWidthLeftBack,
                                        [styles.rightBack]: isStartWidthRightBack || isEndWidthRightBack,
                                    })
                                    }
                                        onClick={() => {
                                            if (!sub.disable) {
                                                handleSelect(sub.value)
                                            }
                                        }}
                                        key={sub.key}
                                        onMouseEnter={() => {
                                            // setHoverValue(sub.value);
                                            if(!range) {
                                                return 
                                            }
                                            if (sub.disable || (value && value.end) || value === undefined) {
                                                bridge.sync(pickerKey);
                                            } else {
                                                bridge.sync(pickerKey, sub.value);
                                            }
                                        }}
                                        onMouseLeave={()=> {
                                            // setHoverValue(null)
                                            if(!range) {
                                                return 
                                            }
                                            bridge.sync(pickerKey);
                                        }}
                                        >
                                        <span className={styles.minItem}>{sub.title}</span>
                                    </span>
                                )
                            })
                        }
                    </div>
                ))
            }
        </div>
    )
}

export default Month;

