export default "import DatePick from \"component/DatePicker\"\nimport { useState } from \"react\"\nimport styles from './index.less'\nimport dayjs, { Dayjs } from \"dayjs\"\n\nconst disableCb = (t: Dayjs): boolean => t.isBefore(dayjs())\n\nconst DateRangeView = () => {\n    const [monthValue, setMonthValue] = useState([])\n    const [yearValue, setYearValue] = useState([]);\n    const [dayValue, setDayValue] = useState([])\n\n    return (\n        <div>\n             <div className={styles.row}>\n                <div className={styles.text}>日期选择:\n                </div>\n                <DatePick\n                    picker=\"day\"\n                    range\n                    value={dayValue}\n                    onChange={setDayValue}\n                    // disableDate={disableCb}\n                    placeHolder=\"请选择日期\"\n                />\n\n            </div>\n            <div className={styles.row}>\n                <div className={styles.text}>月份选择:\n                </div>\n                <DatePick\n                    picker=\"month\"\n                    range\n                    value={monthValue}\n                    onChange={setMonthValue}\n                    disableDate={disableCb}\n                    placeHolder=\"请选择月份\"\n                />\n\n            </div>\n\n\n\n            <div className={styles.row}>\n                <div className={styles.text}> 年份选择:\n                </div>\n                <DatePick\n                    picker=\"year\"\n                    range\n                    value={yearValue}\n                    onChange={setYearValue}\n                    disableDate={disableCb}\n                    placeHolder=\"请选择年份\"\n                />\n\n            </div>\n\n        </div>\n    )\n}\n\nexport default DateRangeView";